<template>
    <transition name="modal">
        <div v-if="showAlert">

            <div class="modal-mask">

                <div class="modal-wrapper">

                    <div class="modal-container ">

                        <div class="modal-header p-3 shadow " :class="Type_Head">
                            <slot name="header">

                                <div class="center  text-white">
                                    {{ head_show }}
                                </div>
                            </slot>
                        </div>

                        <div class="modal-body ">
                            <slot name="body">

                                !{{ body_show }}
                            </slot>
                        </div>

                        <div class="modal-footer">
                            <slot name="footer">

                                <div v-if="Button_Type">
                                    <div class="btn btn-secondary mx-2" @click="Close">ยกเลิก</div>

                                    <div class="btn btn-success" @click="Confirm">ตกลง</div>
                                </div>
                                <div v-else>
                                    <div class="btn btn-outline-secondary  mx-2" @click="Close">ตกลง</div>


                                </div>
                            </slot>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>





</template>
    
<script>
import axios from 'axios';

export default {
    name: "Alert_show",

    props: {
        showAlert: Boolean,
        data: null,
    },

    data() {
        return {
           
        }

    },
    computed: {

        Button_Type() {
            if (this.data.type == "success") {
                return true
            } else {
                return false
            }



        },
        Type_Head() {
            const type = `bg-${this.data.type}`;

            return type
        },
        head_show() {
            return this.data.head
        },
        body_show() {
            return this.data.body
        }
    },
    mounted() {





    },
    methods: {
        Close(ev) {

            this.$emit("close");
        }, Confirm(ev) {
            this.$emit("confirm")
        }
    }
};
</script>
    
    
<style scoped>
/* we will explain what these classes do next! */
input {
    border-top-style: hidden;
    border-right-style: hidden;
    border-left-style: hidden;
    border-bottom-style: hidden;
    background-color: rgb(255, 255, 255);
    width: 100%;

}

.no-outline:focus {
    outline: none;
}

.modal-footer {

    padding: 10px;

}



.modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: table;
    transition: opacity 1s ease;
}

.modal-wrapper {
    display: table-cell;
    vertical-align: middle;
}

.modal-container {
    width: 80%;
    height: 30%;
    margin: 0px auto;

    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
    transition: all 0.3s ease;
    font-family: Helvetica, Arial, sans-serif;
}

.modal-header {
    margin-top: 5%;

    color: #030303;
    font-size: large;
    font-weight: 900;

}

.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.modal-body {
    margin: 30px 20px 10px 20px;
}

.modal-default-button {
    float: right;
}


.modal-enter {
    opacity: 0;
}

.modal-leave-active {
    opacity: 0;
}

.modal-enter-active,
.modal-leave-active {
    transition: opacity 0.01s ease;
}


.modal-enter-from,
.modal-leave-to {
    opacity: 0;
}


@media (min-width: 600px) {
    .modal-mask {
        position: fixed;
        z-index: 9998;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        display: table;
        transition: opacity 1s ease;
    }

    .modal-container {
        width: 30%;
        height: 80%;
        margin: 0px auto;
        padding: 20px 30px;
        background-color: #fff;
        border-radius: 2px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
        transition: all 0.3s ease;
        font-family: Helvetica, Arial, sans-serif;
    }
}
</style>